<template> 
    <router-view/>
</template>

<script>
export default {
  created () {
    if (!sessionStorage.getItem("UserName") ){
      this.$router.push({ name: 'user'}).catch(err => (this.$message.error(err)))
    }
    window.addEventListener("beforeunload",()=>{  
      let data = sessionStorage.getItem("UserName")
      this.$store.commit("updateUserName", data)
      window.sessionStorage.setItem('UserName', this.$store.state.UserName);

    })
  },
  mounted(){
    window.addEventListener("load",()=>{  
      if(sessionStorage.getItem("UserName")){
        let data = sessionStorage.getItem("UserName")
        this.$store.commit("updateUserName", data)
      }
      
    })
  }
}
</script>

<style>
body {
  margin:0px
}
</style>
