let front = "http://192.168.177.242:8000/"
import myaxios from 'axios'
const axios = myaxios.create({
    timeout: 3000
});

axios.interceptors.response.use(function (response) {
    return response;
}, function (error) {
    return Promise.reject(error);
});



Date.prototype.format = function (fmt) {
    var o = {
      "M+": this.getMonth() + 1,                   //月份
      "d+": this.getDate(),                        //日
      "h+": this.getHours(),                       //小时
      "m+": this.getMinutes(),                     //分
      "s+": this.getSeconds(),                     //秒
      "q+": Math.floor((this.getMonth() + 3) / 3), //季度
      "S": this.getMilliseconds()                  //毫秒
    };
  
    //  获取年份 
    if (/(y+)/i.test(fmt)) {
      fmt = fmt.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length));
    }
  
    for (var k in o) {
      if (new RegExp("(" + k + ")", "i").test(fmt)) {
        fmt = fmt.replace(
          RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
      }
    }
    return fmt;
  }
function myget(type,payload,query) {
    return new Promise((resolve, reject) =>{
       
        function success(text){
            let obj = JSON.parse(text);// eslint-disable-line no-unused-vars
            resolve(obj) 
        }
        function fail(text){
            reject(text)
        }
        var request = new XMLHttpRequest()  

        request.onreadystatechange = function () { 
            if (request.readyState === 4) { 
                if (request.status === 200) {
                    success(request.response);
                } else {
                    fail(request.status)
                }
            }
        }
        switch (type){
            case "GET":
                request.open(type, front+payload+query, true)
                request.send()
                break;
            case "POST":
                request.open(type, front+payload, true)
                request.send(query)
                break;
        }
        
    })
}
function AxiosDelete(query,data){// eslint-disable-line no-unused-vars
    const InfoId = data// eslint-disable-line no-unused-vars
    const url = front + query+`${InfoId}`
    return axios.delete(url)
}
function AxiosAdd(query,data){
    const url = front +query
    console.log(data)
    return axios.post(url,data)
}
function InfoAdd(query,data){
    const url = front +query
    return axios.post(url,"",{ // 在post中第三个是附加参数，第二个是数据
        params:{
            title :data.Title,
            content : data.Content,
            username : data.username,
            
        }  
    })
}
function TodoAdd(query,data){
    const url = front +query
    return axios.post(url,"",{
        params:{
            title :data.Title,
            username : data.username
        }
    })
}
function AxiosGet(query, payload){// eslint-disable-line no-unused-vars
    let url = front + query ;
    return axios.get(url, {
        params: {
            username: payload
        }
    })
}
function SearchInfo(data){
    let url = front + 'infos/'+`${data}`;
    return axios.get(url)
}
function fixInfo(data ,formData){
    let url = front + 'infos/'+`${data}`
    return axios.put(url, "",{
        params:{
            usrname:formData.username,
            title:formData.title,
            content:formData.content,
        }
    })
}
function fixTodo(data ,formData){
    let url = front + 'todolist/'+`${data}`
    return axios.put(url,"", {
        params:{
            usrname:formData.username,
            title:formData.title,
        }
    })
}
function Register(data){
    let url = front + 'users/register'
    return axios.get(url,{
        timeout: 3000,
        params : data
    })
}
function TodoPut(data){
    let url = front + 'todolist/'+`${data}`
    return axios.put(url,{
        timeout: 3000
    })
}
function InfoTodo(name,data){
    let url = front + 'infos/'+`${data}`+'/move'
    return axios.post(url,"",{
        params: {
            username : name
        }
    })
}
function AvatarGet(data){
    let url = "http://localhost:3000/avatar"
    return axios.get(url,{
        params: {
            username : data
        }
    })

}
export {
    AvatarGet,
    Register,
    myget,
    AxiosDelete,
    AxiosGet,
    AxiosAdd,
    InfoAdd,
    SearchInfo,
    fixInfo,
    fixTodo,
    TodoAdd,
    TodoPut,
    InfoTodo,
}

