<template >
    <el-header>
        <el-row>
            <el-col :span="4"
            >
                <span
                class="Title">Glimmer Space</span>
            </el-col>
            <el-col 
            :offset="12"
            :span="6">
                    <el-input
                    placeholder="请输入内容"
                    v-model="search">
                    <i slot="prefix" class="el-input__icon el-icon-search"></i>
                </el-input>
            </el-col>
            <el-col :span="2">
                <el-button plain 
                icon="el-icon-search"
                @click="Search"
                >搜索</el-button>
            </el-col>
        </el-row>
            
    </el-header>
</template>

<script>
import { SearchInfo} from '../../util/submit.js'
export default {
    components:{
    },
    computed:{
        name() {
            return this.$store.state.UserName
        }
    },
    data() {
        return{
            search:""
        }
    },
    methods:{
        Search(message){
            let search = SearchInfo(this.search)
            search.then((res)=>{
                console.log(res)
            }).catch((err)=>{
                console.log(err)
            })
            console.log(message)
        }
    }
}
</script>
<style scoped>
.Title{
    font-size: 18px;
    font-weight: bold;
}
</style>